import { Component, OnInit, ViewChild } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { AuthUserService } from 'src/app/services/auth-user.service';
import { UtilArrayService } from 'src/app/services/util-array.service';
import { NavController, IonInfiniteScroll } from '@ionic/angular';

@Component({
  selector: 'app-list',
  templateUrl: './list.page.html',
  styleUrls: ['./list.page.scss'],
})
export class ListPage implements OnInit {

  @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;


  public notifications = [];
  public notificationModel : Model;
  public userModel : Model;

  firstLoading = true;
  dataPaginate : any;

  constructor(
    public request : RequestService,
    public userAuth : AuthUserService,
    public utilArray : UtilArrayService,
    public navCtrl : NavController
  ) {
    this.notificationModel = new Model('Notification',request);
    this.userModel = new Model('User',request);
   }

  ngOnInit() {

    this.notifications = this.userAuth.user.notifications ? this.userAuth.user.notifications : [] ;

    this.init();
  }


  async init($event = null,paginate = false){

    let page = {};
    
    if(paginate && this.dataPaginate ){
      page = {
        page : this.dataPaginate.current_page + 1
      }
    }

    this.userModel.api_functionModel(this.userAuth.user.id , 'resetNotifications').subscribe(()=>{
      console.log('restart count  notifications')
      this.userAuth.user.counts_not = 0;
      this.userAuth.saveUser();
    })

    
    this.userModel.api_functionModel(this.userAuth.user.id ,'pageNotifications',page).subscribe(
      response => {
        console.log(response);

        if(response['status'] == 'success'){
          this.dataPaginate = response['data'];
          if(!paginate){
            this.notifications = response['data']['data']
          }else{

            for(let not of response['data']['data']){
              this.notifications.push(not);
            }
            
          }
        }

        if($event)
          $event.target.complete();
        
        this.firstLoading = false;

      },
      error => {
        console.log(error)
        if($event)
          $event.target.complete();
      }
    )
  }

  openModal(){

  }

  loadData(event) {
    if(this.dataPaginate &&  this.dataPaginate.last_page == this.dataPaginate.current_page){
      event.target.complete();
      event.target.disabled = true;
      this.infiniteScroll.disabled = !this.infiniteScroll.disabled;
      return;
    }

    this.init(event,true);
  }

  visibleNot(not){
    if(not.id)
      this.notificationModel.api_functionModel(not.id,'viewed').subscribe(
        (response)=>{
          let notification = response['data'];
          this.utilArray.listUpdate(this.notifications ,notification.id , notification);
      })

    this.navCtrl.navigateForward(not.route);
  }

}
